.flight-search-app {
  @import "variables";
  @import "mixins";
  .search-form {
    padding-left: 15px;
    padding-right: 15px;
    .no-padding-row {
      margin-top: 15px;
      @media screen and (max-width: $screen-xs) {
        margin-top: 0;
      }
      .col-sm {
        padding-left: 0;
        padding-right: 0;
        margin-right: -1px;
        @media screen and (max-width: $screen-xs) {
          margin-bottom: 15px;
          label {
            margin-bottom: 0;
          }
        }
        .btn {
          min-height: 40px;
          margin-top: 33px;
          border-radius: 0;
          min-width: 100px;
          @media screen and (max-width: $screen-xs) {
            margin-top: 0;
            min-width: 100%;
          }
        }
      }
    }
    .form-item {
      border: 1px solid $grey999;
      &.icon-field {
        position: relative;
        .fa {
          width: 16px;
          height: 16px;
          font-size: 16px;
          position: absolute;
          top: 0;
          right: auto;
          bottom: 0;
          left: 5px;
          display: block;
          margin: auto;
          z-index: 2;
          pointer-events: none;
        }
        &.date-picker {
          .fa {
            left: auto;
            right: 5px;
          }
        }
      }
      select.form-control {
        padding: 6px 6px;
      }
    }
    .custom-control {
      @media screen and (max-width: $screen-xs) {
        margin-bottom: 15px;
      }
    }
    label {
      font-weight: 500;
    }
    .form-row.no-padding-row {
      .multi-city-fields {
        .col-sm {
          button {
            background-color: $lightF7;
            display: inline-block;
            position: relative;
            width: 40px;
            height: 40px;
            margin-top: 33px;
            margin-left: 10px;
            border: 1px solid $grey999;
            .fa {
              position: absolute;
              display: block;
              top: 0;
              right: 0;
              bottom: 0;
              left: 0;
              margin: auto;
              font-size: 24px;
              color: $golden;
              width: 24px;
              height: 24px;
            }
            &:hover,
            &:focus,
            &:active {
              outline: none;
            }
          }
        }
      }
      &.multi {
        .multi-city-fields {
          display: block;
        }
        .from-to-fields {
          display: none;
        }
      }
    }
    &.flights-page-control {
      .form-row.no-padding-row {
        .col-sm {
          @media screen and (min-width: $screen-sm) {
            &.multi-city-fields,
            &.from-to-fields {
              min-width: 657px;
              max-width: 657px;
              .row {
                margin-left: 0;
                margin-right: 0;
              }
            }
            &.search-column {
              min-width: 200px;
              max-width: 200px;
            }
            &.dp-column {
              min-width: 130px;
              max-width: 130px;
            }
            &.cabin-column {
              min-width: 110px;
            }
            &.sel-column {
              min-width: 60px;
              max-width: 60px;
            }
          }
          @media screen and (max-width: $screen-xs) {
            &.from-to-fields {
              margin-bottom: 0;
            }
          }
        }
        .multi-city-fields {
          display: none;
          > .row {
            margin-bottom: 15px;
            .col-sm {
              &.search-column {
                min-width: 235px;
                max-width: 235px;
              }
              &:nth-of-type(3) {
                min-width: 130px;
                max-width: 130px;
              }
              &:nth-of-type(4) {
                min-width: 50px;
                max-width: 50px;
              }
            }
          }
        }
      }
      label {
        color: $black;
      }
      .form-item {
        &.icon-field {
          .fa {
          }
          .form-control {
            padding-left: 25px;
          }
          &.date-picker {
            .form-control {
              padding-left: 12px;
              padding-right: 25px;
            }
          }
        }
        select.form-control {
          padding: 6px 6px;
        }
      }
      .col {
        .btn {
          height: 40px;
        }
      }
    }
  }
}
