.flight-search-app {
  @import "bootstrap-scss/bootstrap";
  @import "variables";
  @import "mixins";
  @import "base_structure";
  .form-row {
    margin-right: -$form-grid-gutter-width;
    margin-left: -$form-grid-gutter-width;
    > .col,
    > [class*="col-"] {
      padding-right: $form-grid-gutter-width;
      padding-left: $form-grid-gutter-width;
    }
  }
  /* Preloader */
  .preloader {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 2;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    &:after {
      content: '';
      display: block;
      position: absolute;
      z-index: 9;
      background-color: #FF007F;
      width: 30px;
      height: 30px;
      margin: auto;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      border-radius: 50%;
      -webkit-animation: preloader 5s linear infinite;
      -moz-animation: preloader 5s linear infinite;
      animation: preloader 5s linear infinite;
    }
  }
  @-webkit-keyframes preloader {
    0% {
      background-color: #FF007F;
      -webkit-transform: scale(0);
      opacity: 1;
    }
    20% {
      background-color: #FF007F;
      -webkit-transform: scale(3);
      opacity: 0;
    }
    20.00001% {
      background-color: #3EA7F3;
      -webkit-transform: scale(0);
      opacity: 1;
    }
    40% {
      background-color: #3EA7F3;
      -webkit-transform: scale(3);
      opacity: 0;
    }
    40.00001% {
      background-color: #79C742;
      -webkit-transform: scale(0);
      opacity: 1;
    }
    60% {
      background-color: #79C742;
      -webkit-transform: scale(3);
      opacity: 0;
    }
    60.00001% {
      background-color: #F57B3D;
      -webkit-transform: scale(0);
      opacity: 1;
    }
    80% {
      background-color: #F57B3D;
      -webkit-transform: scale(3);
      opacity: 0;
    }
    80.00001% {
      background-color: #04D9D9;
      -webkit-transform: scale(0);
      opacity: 1;
    }
    100% {
      background-color: #04D9D9;
      -webkit-transform: scale(3);
      opacity: 0;
    }
  }
  @-moz-keyframes preloader {
    0% {
      background-color: #FF007F;
      -moz-transform: scale(0);
      opacity: 1;
    }
    20% {
      background-color: #FF007F;
      -moz-transform: scale(3);
      opacity: 0;
    }
    20.00001% {
      background-color: #3EA7F3;
      -moz-transform: scale(0);
      opacity: 1;
    }
    40% {
      background-color: #3EA7F3;
      -moz-transform: scale(3);
      opacity: 0;
    }
    40.00001% {
      background-color: #79C742;
      -moz-transform: scale(0);
      opacity: 1;
    }
    60% {
      background-color: #79C742;
      -moz-transform: scale(3);
      opacity: 0;
    }
    60.00001% {
      background-color: #F57B3D;
      -moz-transform: scale(0);
      opacity: 1;
    }
    80% {
      background-color: #F57B3D;
      -moz-transform: scale(3);
      opacity: 0;
    }
    80.00001% {
      background-color: #04D9D9;
      -moz-transform: scale(0);
      opacity: 1;
    }
    100% {
      background-color: #04D9D9;
      -moz-transform: scale(3);
      opacity: 0;
    }
  }
  @keyframes preloader {
    0% {
      background-color: #FF007F;
      transform: scale(0);
      opacity: 1;
    }
    20% {
      background-color: #FF007F;
      transform: scale(3);
      opacity: 0;
    }
    20.00001% {
      background-color: #3EA7F3;
      transform: scale(0);
      opacity: 1;
    }
    40% {
      background-color: #3EA7F3;
      transform: scale(3);
      opacity: 0;
    }
    40.00001% {
      background-color: #79C742;
      transform: scale(0);
      opacity: 1;
    }
    60% {
      background-color: #79C742;
      transform: scale(3);
      opacity: 0;
    }
    60.00001% {
      background-color: #F57B3D;
      transform: scale(0);
      opacity: 1;
    }
    80% {
      background-color: #F57B3D;
      transform: scale(3);
      opacity: 0;
    }
    80.00001% {
      background-color: #04D9D9;
      transform: scale(0);
      opacity: 1;
    }
    100% {
      background-color: #04D9D9;
      transform: scale(3);
      opacity: 0;
    }
  }
  .blue {
    color: $blue;
  }
  .form-control:disabled, .form-control[readonly] {
    background-color: $white;
  }
  section:not(.zero-padding) {
    padding: 30px 0;
  }
  main {
    min-height: calc(100vh - 550px);
  }
  input[readonly="readonly"] {
    background-color: $lightF7 !important;
  }
  .dob-info {
    display: none;
    color: $danger;
    white-space: nowrap;
    font-size: 12px;
  }
  .is-invalid + .dob-info {
    display: block;
    margin-top: 5px;
  }
  .alert-danger p span {
    display: block !important;
  }
  .ids-info,
  .dev-box {
    display: none !important;
  }
  /** Styles only for localhost **/
  .localhost {
    .ids-info,
    .dev-box {
      width: 100% !important;
      display: block !important;
    }
    input[required="required"] {
      background-color: #efc4c4 !important;
    }
    .ids-info {
      font-weight: bold;
      color: $danger;
      margin-top: 20px !important;
    }
  }
  .home-page-search-box {
    .form-item {
      margin-bottom: 10px !important;
    }
    .search-form {
      min-width: 440px !important;
      max-width: 440px !important;
      margin-left: auto !important;
      margin-right: auto !important;
    }
    .multi-city-fields {
      min-width: 432px !important;
      max-width: 432px !important;
      padding-left: 15px !important;
      padding-right: 15px !important;
      .search-column {
        min-width: 36% !important;
        max-width: 36% !important;
      }
    }
    .from-to-fields {
      padding-left: 15px !important;
      padding-right: 15px !important;
      .search-column {
        min-width: 432px !important;
        max-width: 432px !important;
      }
      .dp-column {
        min-width: 216px !important;
        max-width: 216px !important;
      }
    }
    .sel-column,
    .cabin-column {
      min-width: 108px !important;
      max-width: 108px !important;
    }
  }
}
#wrapper {
  max-width: 2560px;
  margin-left: auto;
  margin-right: auto;
}
